<template>
  <div id="login-main">
    <div class="left">
      <div class="top">
        <div style="float: left; margin-right: 10px;">
          <img src="../assets/ifi-logo.png" alt="ifiLogo" style="width: 50px;height: 50px">
        </div>
        {{ $t('LoginView.title') }}
      </div>
      <div class="bottom">
        <div class="left-img">
          <img v-if="img == 1" src="@/assets/login-box.svg" style="width: 70%">
          <img v-if="img == 2" src="@/assets/login-box-02.svg" style="width: 80%; margin-bottom: 50px">
          <h2 style="margin-bottom: 10px">{{ $t('LoginView.titleBox') }}</h2>
          <el-text class="mx-1" type="info">{{ $t('LoginView.content') }}</el-text>
        </div>
      </div>
    </div>

    <div class="right">
      <div class="top">
        <el-dropdown @command="switchover" style="float: right">
          <span class="el-dropdown-link">
            <svg style="color: white; width: 25px; height: 25px" preserveAspectRatio="xMidYMid meet" viewBox="0 0 24 24" width="1.2em" height="1.2em" data-v-12008bb2=""><path fill="currentColor" d="m18.5 10l4.4 11h-2.155l-1.201-3h-4.09l-1.199 3h-2.154L16.5 10h2zM10 2v2h6v2h-1.968a18.222 18.222 0 0 1-3.62 6.301a14.864 14.864 0 0 0 2.336 1.707l-.751 1.878A17.015 17.015 0 0 1 9 13.725a16.676 16.676 0 0 1-6.201 3.548l-.536-1.929a14.7 14.7 0 0 0 5.327-3.042A18.078 18.078 0 0 1 4.767 8h2.24A16.032 16.032 0 0 0 9 10.877a16.165 16.165 0 0 0 2.91-4.876L2 6V4h6V2h2zm7.5 10.885L16.253 16h2.492L17.5 12.885z"></path></svg>
          </span>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item command="zhCn">
                <el-icon><Football /></el-icon> {{ $t("HomeView.language.zh") }}
              </el-dropdown-item>
              <el-dropdown-item command="en">
                <el-icon><Help /></el-icon> {{ $t("HomeView.language.en") }}
              </el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
      <div class="login">
        <div class="right-login">
          <h1 style="margin-bottom: 20px">{{ $t('LoginView.login') }}</h1>
          <el-form :model="userForm" :rules="rules" ref="userForm">
            <el-form-item prop="username">
              <el-input type="text" size="large" v-model="userForm.username" prefix-icon="User" :placeholder="$t('LoginView.accountPlaceholder')"></el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input type="password" size="large" show-password v-model="userForm.password" prefix-icon="Unlock" :placeholder="$t('LoginView.passwordPlaceholder')"></el-input>
            </el-form-item>
          </el-form>
          <div class="buttLogin">
            <el-button type="primary" @click="btnlogin('userForm')">
              {{ $t('common.button.login') }}
            </el-button>
            <el-button @click="resetForm('userForm')">
              {{ $t('common.button.reset') }}
            </el-button>
          </div>

          <div class="etBox">
            <el-divider>{{ $t('LoginView.titleUrl') }}</el-divider>
            <el-row :gutter="20">
              <el-col :span="6"><div class="grid-content ep-bg-purple" />
                 <el-link type="info" icon="Star" :href="goUrl.home">
                   {{ $t('LoginView.afterPlatform') }}
                 </el-link>
              </el-col>
              <el-col :span="6"><div class="grid-content ep-bg-purple" />
                 <el-link type="info" icon="User" :href="goUrl.clientele">
                   {{ $t('LoginView.clienteleAfter') }}
                 </el-link>
              </el-col>
              <el-col :span="6"><div class="grid-content ep-bg-purple" />
                 <el-link type="info" icon="Tickets" :href="goUrl.official">
                   {{ $t('LoginView.officialWebsite') }}
                 </el-link>
              </el-col>
              <el-col :span="6"><div class="grid-content ep-bg-purple" />
                 <el-link type="info" icon="Monitor" :href="goUrl.store">
                   {{ $t('LoginView.officialMall') }}
                 </el-link>
              </el-col>
            </el-row>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {login} from '../http/api.js'
import {ElMessage} from "element-plus";
import en from 'element-plus/dist/locale/en.mjs'
import zhCn from 'element-plus/dist/locale/zh-cn.mjs'

export default {
  name: 'LoginView',
  data: function () {
    return {
      img: Math.floor(Math.random() * 2) + 1,
      userForm: {
        username: '',
        password: ''
      },
      rules: { // 编写表单验证规则
        username: [
          {required: true, message: this.$t('LoginView.accountPlaceholder'), trigger: 'blur'},
        ],
        password: [
          {required: true, message: this.$t('LoginView.passwordPlaceholder'), trigger: 'blur'},
        ]
      },
      goUrl: {
        // home: process.env.VUE_APP_BACKEND_WEB_URL + '/login',
        home: 'http://121.43.50.177:9004/#/login',
        clientele: process.env.VUE_APP_BACKEND_WEB_URL + '/clientele',
        official: 'https://element-plus.org/zh-CN/component/button.html',
        store: 'https://www.csdn.net/?spm=1018.2226.3001.4476',
      }
    }
  },
  created() {
    // 根据 tocken 进行对应的跳转页面
    this.tockengoto();
  },
  methods: {

    tockengoto() {
      //获取tocken， 如果token为空， 表明需要登录
      let token = localStorage.getItem('token');
      if (!token) {
        return;
      }
      //token不为空且没有过期，直接跳转/home
      // let exptime = JSON.parse(atob(token.split('\.')[1])).exp;
      // let nowtime = new Date().getTime() / 1000;
      // if (exptime > nowtime) {
      //   this.$router.push('/home');
      // }
    },

    btnlogin: function (formName) {
      //调用表单验证结果
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let formData = new URLSearchParams({
            "account": this.userForm.username,
            "password": this.userForm.password
          });

          // debugger
          // 提交表单
          login(formData.toString()).then((res) => {
            if (res.data.status == 200) {
              // 保存服务器返回的token
              let userInfo = JSON.parse(res.data.data);
              window.localStorage.setItem('token', userInfo.token);
              window.localStorage.setItem('account', res.data.data);
              // 跳转到首页
              // this.$router.push({name: 'home'});
              this.$router.push('/home');
              ElMessage({
                message: res.data.msg,
                type: 'success',
              })
            } else {
              ElMessage.error(res.data.msg)
            }
          }).catch((erro) => {

          });
        } else {
          return false;
        }
      });

    },
    // 重置表单
    resetForm: function (formName) {
      this.$refs[formName].resetFields();
    },

    // 子组件调用父组件的方法 切换语言 值必须是语言文件的引入名称
    switchover(command) {
      if (command == 'en') {
        this.$confirm(this.$t("HomeView.languageDialog.enContent"), this.$t("HomeView.languageDialog.title"), {
          confirmButtonText: this.$t("common.button.confirm"),
          cancelButtonText: this.$t("common.button.cancel"),
          type: 'success'
        }).then(() => {
          this.gotolanguage('en');
        });
      } else {
        this.$confirm(this.$t("HomeView.languageDialog.zhContent"), this.$t("HomeView.languageDialog.title"), {
          confirmButtonText: this.$t("common.button.confirm"),
          cancelButtonText: this.$t("common.button.cancel"),
          type: 'success'
        }).then(() => {
          this.gotolanguage('zh');
        });
      }
    },
    // 切换语言 刷新首页框架数据 重新获取菜单 重新获取标签 重新获取标题 ...
    gotolanguage(locale) {
      this.$i18n.locale = locale; // 切换 I18n 语言包 选项式写法
      if (locale == 'en') {
        this.$emit('childClick', en);
        window.localStorage.setItem("navigatorLanguage", 'en-US');
      } else if (locale == 'zh'){
        this.$emit('childClick', zhCn);
        window.localStorage.setItem("navigatorLanguage", 'zh-CN');
      }
      // 刷新网页
      location.reload();
    },

  }
}
</script>

<!-- <style scoped> 表示此样式只在本组件有效 否则有可能影响其他组件 -->
<style scoped>
#login-main {
  display: flex;
  height: 100%;
  width: 100%;
}

.left {
  width: 50%;
  background-color: #353e54; /* 左侧div的背景颜色 */
  padding: 35px;
  display: flex;
  flex-direction: column;
}
.top {
  height: 50px;
  color: white;
  font-size: 20px;
  font-weight: bold;
  line-height: 50px;
}
.bottom {
  flex: 1;
  position: relative; /* 添加相对定位 */
}
.left-img {
  color: white;
  position: absolute;
  top: 50%; /* 将元素的顶部与其包含块的中部对齐 */
  left: 50%; /* 将元素的左侧与其包含块的中部对齐 */
  transform: translate(-50%, -50%); /* 使用transform进行居中 */
}

.right {
  width: 50%;
  background-color: #293146; /* 右侧div的背景颜色 */
  display: flex;
  flex-direction: column;
  padding: 30px;
}

.login {
  flex: 1;
  position: relative; /* 添加相对定位 */
}
.right-login{
  /*width: 450px;*/
  width: 50%;
  color: white;
  position: absolute;
  top: 50%; /* 将元素的顶部与其包含块的中部对齐 */
  left: 50%; /* 将元素的左侧与其包含块的中部对齐 */
  transform: translate(-50%, -50%); /* 使用transform进行居中 */
}
/* 组件表单覆盖 背景颜色和字体颜色 */
/deep/ .el-input__wrapper{
  background-color: transparent !important;
}
/deep/ .el-input__inner{
  color: white;
}
/* 浏览器自动填充的背景颜色 字体颜色 */
/deep/ .el-input__inner:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px #293146 inset !important;
  -webkit-text-fill-color: white !important;
}
.buttLogin{
  text-align: center;
  margin-top: 20px;
}
.etBox{
  margin-top: 55px;
  text-align: center;
}
/deep/ .el-divider__text{
  background-color: #293146;
  color: white;
}

</style>
