import request from './request'

// 用户登录
export function login(data) {
    return request({
        url: '/api/login',
        method: 'post',
        data
    })
}

// 获取用户菜单权限
export function getMenuList() {
    return request({
        url: '/api/resourceInfo/getMenu',
        method: 'get'
    })
}

